import sPopup from '@/components/s-popup/index';
export default {
	components: {
	   sPopup
	 },
	data() {
		return {
			visible:false,
			isshowinfo:true,
			uid:'',
			token:'',
			data:{},
			amount:''
		}
	},
	mounted(){
		var userInfo=uni.getStorageSync('userInfo')
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
			this.getDetail()
		}
	},
	methods: {
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		to(where){
			uni.navigateTo({
				url:`./${where}`
			})
		},
		getDetail(){
			this.$loading()
			var that=this
			var params={
				uid:this.uid,
				token:this.token,
			}
			var url='&r=api.member.commission'
			  this.$apiPost(url,params).then((res) =>{
				  that.data=res.data
				  uni.hideLoading()
			  })
		},
		startWithdraw(){
			this.visible=true
		},
		oninput(e){
			var v=parseInt(e.detail.value)
			var c=parseInt(this.data.canCommission)
			if(v>=c){
				this.amount=c
			}else if(v<=1){
				this.amount=1
			}
		},
		withdraw(){
			if(this.amount>this.data.canCommission){
				this.$msg('超过可提现金额')
				return ;
			}
			this.$loading()
			var that=this
			var params={
				uid:this.uid,
				token:this.token,
				amount:this.amount
			}
			var url='&r=api.member.commission.withdrawal'
			  this.$apiPost(url,params).then((res) =>{
				  uni.hideLoading()
				  that.$msg('提交成功，请等待审核')
				  setTimeout(function(){
					  that.visible=false
					  that.amount=''
					  that.getDetail()
				  },1000)
			  })
		}
	}
}
